:root {
  --lime-green: hsl(163, 72%, 41%);
  --bright-red: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle1: hsl(210, 78%, 56%);
  --toggle2: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

/*@function rem($pixels, $context: 16) {
    @return (math.div($pixels, $context)) * 1rem;
}*/
h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text2);
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  padding-bottom: 1.625rem;
  border-bottom: 0.0625rem solid var(--dark-text1);
  border-radius: 0.0625rem;
  margin-bottom: 1.375rem;
}
@media (min-width: 71.875em) {
  .header__title {
    width: auto;
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}
.header__toggle {
  color: var(--dark-text1);
}
@media (min-width: 40em) {
  .header__toggle {
    color: inherit;
  }
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.3rem 0.5rem;
  margin: 0;
  margin-left: auto;
  font-weight: 700;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=ligh] {
  grid-column: 3/4;
  justify-self: start;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem;
  opacity: 0;
  height: 1.5rem;
  width: 1.25rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.0625rem solid var(--dark-text2);
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  pointer-events: none;
  background: linear-gradient(225deg, var(--toggle2) 0%, var(--toggle1) 98.02%);
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 0.15s ease-in-out;
  /*
  #light:checked {
      left: calc(100% - 21px);
      right: rem(3);
  }
  */
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 0.1875rem;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cardcontainer {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 13.25rem);
  gap: 1.5rem;
  margin: 0 1.625rem 1.25rem;
  max-width: 69.375rem;
}
@media (min-width: 40em) {
  .cardcontainer {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 13.25rem;
    gap: 1.5rem;
    margin: 0 auto 1.25rem;
  }
}
.cardcontainer .card {
  border-top: 0.25rem solid;
  border-radius: 0.25rem;
  background-color: var(--dark-card-bg);
  text-align: center;
  font-size: 0.875rem;
  font-weight: 700;
}
.cardcontainer .card__header__id {
  vertical-align: top;
  padding-left: 0.5rem;
  color: var(--dark-text1);
}
.cardcontainer .card__title {
  font-size: 2.875rem;
  line-height: 2.875rem;
  display: block;
  width: 100%;
}
.cardcontainer .card__subtitle {
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  font-weight: 10;
  color: var(--dark-text1);
}
.cardcontainer .card__footer {
  color: var(--lime-green);
}
.cardcontainer .card__footer__logo {
  vertical-align: middle;
}
.cardcontainer .card__youtube .card__footer {
  color: var(--bright-red);
}
.cardcontainer .card__facebook {
  border-color: var(--facebook);
}
.cardcontainer .card__twitter {
  border-color: var(--twitter);
}
.cardcontainer .card__instagram {
  border: none;
}
.cardcontainer .card__instagram__gradient {
  display: block;
  background: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
  width: 100%;
  height: 0.25rem;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
}
.cardcontainer .card__youtube {
  border-color: var(--youtube);
}
.cardcontainer .card > div:first-child, .cardcontainer .card > div:last-child, .cardcontainer .card__instagram .card__header {
  padding: 1.75rem 0;
}